      <div class="login-box">
        <div class="left-box">
          <h2>你好，朋友！</h2>
          <p class="title">欢迎使用风植管理系统</p>
        </div>
        <!-- 登录表单 -->
        <el-form
          label-position="left"
          :model="loginForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="60px"
          class="login-container"
        >
          <el-form-item label="账号" prop="username">
            <el-input
              type="text"
              v-model="loginForm.username"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              type="password"
              v-model="loginForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item class="third">
            <el-button round type="primary" @click="submitForm('ruleForm')"
              >登录</el-button
            >
            <el-button round @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
